import { Plugin } from '@ali/4ever-cangjie';
import createInlineNodeOnSpace from '../utils/createInlineNodeOnSpace';

const onCangjieInput: Plugin['onCangjieInput'] = (event, controller, next) => {
  if (event.detail.data !== ' ') return next();
  const onSpace = createInlineNodeOnSpace('`', 1, false);
  return onSpace(event, controller, next);
};

export default onCangjieInput;
